import React, { Component } from 'react';
import styled from 'styled-components';

import MID from './nineRegions/MID'
import SAC from './nineRegions/SAC'
import ENC from './nineRegions/ENC'
import ESC from './nineRegions/ESC'
import WNC from './nineRegions/WNC'
import WSC from './nineRegions/WSC'
import MTN from './nineRegions/MTN'
