// cursor: pointer; // seems not to work
// https://stackoverflow.com/questions/26672936/cursor-pointer-on-svg-element-is-not-working
// Solution: putting it on mapframe --it's just a div, after all. //

const StyledRegionMap = styled.div`
position: relative;
height: 100%;

svg {
    & #home-prompt {
        fill: #72794d;
        fill: fuchsia; //   remove this !
    }
    & #americaRegions {
        & text {
            opacity: 0;
        }
        & .home{
            opacity: 0.2;
            stroke-width: 0.5px;
            &:hover {
                opacity: 1;
                stroke-width: 1px;
                stroke: lightgreen;
                stroke-opacity: 1;
                &  .text-label {
                    fill: #72794d;
                    stroke-width: 0;
                    opacity: 1;
                }   
            }
        }
    }
}

// rule for state labels in svg monitor "buttons"
svg > svg .home-result{
        & > text {
        font-size: 50px;
        fill: #FFF;
        opacity: .6;
        font-family:'Arial-Black', sans-serif;
        transform: scale(1, .5);
        &:hover {
         opacity: 1;
        }
    }
}
    svg > svg> text {
        fill: rgba(255, 156, 121, 0.4);
        transform: scale(1.5, 2) translate(-10vw, 10vh);
        
        }
    svg > svg path{
        stroke-width: 0.5px;
        stroke: lightgreen;
    }
`


